import React, {
    useState,
    useCallback,
    useEffect,
    useLayoutEffect
  } from "react";
  import { Pane } from "evergreen-ui";
  import WriteCharactorSearch from "@components/WriteCharactor/WriteCharactorSearch";
 
  export default function() {
    const [char, setChar] = useState("");
    const [content, setContent] = useState([]);
    const onSearch = async slug => {
        const fetchingUrl = `https://ytinfo.benjaminlowry.com/${slug}`
        const res = await fetch(fetchingUrl);
        const value = await res.text() as any;
        setChar(slug);
        console.log(value.player_response);
        if(value &&
            value.player_response 
            && value.player_response.streamingData  
            && value.player_response.streamingData.formats) {
            setContent(value.player_response.streamingData.formats as Array<object>);
        }
    };
  

    useEffect(() => {

    }, [char]);
  

  

  
    return (
      <Pane width="800px" height="100%" margin="auto">
        <WriteCharactorSearch
          char={char}
          onSearch={onSearch}
        ></WriteCharactorSearch>
        {content.map(item => (
            <div>
                {item.url}
                {item.mimeType}
                {item.qualityLabel}
                {item.qualityLabel}
            </div>
        ))}
        <style jsx>{`

        `}</style>
      </Pane>
    );
  }
  